/*content of battery*/
.battery {
	margin: 5px;
	position: relative;
	bottom: 0;
	height: 30px;
	border-radius: 5px;
	float: left;
	width: 95px;
}

/*frame of battery*/
.battery::before {
	content: "";
	position: absolute;
	background-color: transparent;
	border: 2px black solid;
	border-radius: 5px;
	height: 100%;
	width: 95px;
}

/*tiny head of battery*/
.battery::after {
	content: "";
	position: absolute;
	background-color: black;
	border: 2px black solid;
	height: 15px;
	width: 6px;
	margin: 0px 93px;
}

.first-charge {
	-webkit-animation: charge 4s;
	        animation: charge 4s;
	-webkit-animation-delay: 2s;
	        animation-delay: 2s;
	-webkit-animation-fill-mode:forwards;
	        animation-fill-mode:forwards;
}

.second-charge {
	-webkit-animation: charge 4s;
	        animation: charge 4s;
	-webkit-animation-delay: 3s;
	        animation-delay: 3s;
	-webkit-animation-fill-mode:forwards;
	        animation-fill-mode:forwards;
}

.third-charge {
	-webkit-animation: charge 4s;
	        animation: charge 4s;
	-webkit-animation-delay: 4s;
	        animation-delay: 4s;
	-webkit-animation-fill-mode:forwards;
	        animation-fill-mode:forwards;
}

@-webkit-keyframes charge {
	0% { width: 0px; background-color: transparent;}
	10% { width: 10px; background-color: red;}
	20% { width: 20px}
	32% {width: 32px; background-color: yellow;}
	100% {width: 95px; background-color: green;}
}

@keyframes charge {
	0% { width: 0px; background-color: transparent;}
	10% { width: 10px; background-color: red;}
	20% { width: 20px}
	32% {width: 32px; background-color: yellow;}
	100% {width: 95px; background-color: green;}
}


